async function doEdit(params:any) {

  let res: any = await edit(params);
  if (res.code === "1") {
    ElMessage.success(res.message)
  } else {
    ElMessage.error(res.message)
  }
}

async function doDeleteMonitor(params:any) {

  let res: any = await deleteMonitor(params);
  if (res.code === "1") {
    ElMessage.success(res.message)
    refreshParent();
  } else {
    ElMessage.error(res.message)
  }
}

